    getTagsList: async (params) => {
        //  localStorage 
        const savedLocale = localStorage.getItem(`${STORAGE_PREFIX}userLocale`) || 'zh_CN'
        return await request({
            url: '/prompt/get_group_tags',
            method: 'get',
            params: {...params,lang:savedLocale}
        })
    },

