	currentLanguage:function(){
		//translate.check();
		var cookieValue = translate.getCookie('googtrans');
		if(cookieValue.length > 0){
			return cookieValue.substr(cookieValue.lastIndexOf('/')+1,cookieValue.length-1);
		}else{
			return translate.localLanguage;
		}
	},
	/*js translate.currentLanguage end*/
	
	/**
	 * 
 	 * @param languageName  english
	 * 				 enzh-CN v1.x
	 * 														 chinese_simplified english  v2.x
	 */
	changeLanguage:function(languageName){
		//v1.x
		var v1 = ',en,de,hi,lt,hr,lv,ht,hu,zh-CN,hy,uk,mg,id,ur,mk,ml,mn,af,mr,uz,ms,el,mt,is,it,my,es,et,eu,ar,pt-PT,ja,ne,az,fa,ro,nl,en-GB,no,be,fi,ru,bg,fr,bs,sd,se,si,sk,sl,ga,sn,so,gd,ca,sq,sr,kk,st,km,kn,sv,ko,sw,gl,zh-TW,pt-BR,co,ta,gu,ky,cs,pa,te,tg,th,la,cy,pl,da,tr,';
		if(v1.indexOf(','+languageName+',') > -1){
			//v1.x
			console.log('v1v12021v2 http://translate.zvo.cn/41549.html');
			translate.check();
			
			var googtrans = '/'+translate.localLanguage+'/'+languageName;
			
			//
			var s = document.location.host.split('.');
			if(s.length > 2){
				var fanDomain = s[s.length-2]+'.'+s[s.length-1];
				document.cookie = 'googtrans=;expires='+(new Date(1))+';domain='+fanDomain+';path=/';
				document.cookie = 'googtrans='+googtrans+';domain='+fanDomain+';path=/';
			}
			
			translate.setCookie('googtrans', ''+googtrans);
			translate.refreshCurrentPage();
			return;
		}
		
		//v2.x
		//translate.setUseVersion2();
		translate.useVersion = 'v2';
		// true
		if(translate.to != null && translate.to.length > 0){
			//
			if(translate.to != translate.language.getLocal()){
				var isReload = true; //
			}
		}
		
		
		translate.to = languageName;
		translate.storage.set('to',languageName);	//

		/*
			1. 
		*/
		//iframeiframe
		try{
			if(window.self !== window.top){
				if(typeof(window.parent.translate) == 'object' && typeof(window.parent.translate.version) == 'string'){
					//iframe translate,iframe
					if(window.parent.translate.language.getCurrent() != languageName){
						//
						window.parent.translate.changeLanguage(languageName);
					}
				}
			}
		}catch(e){
			//try
			console.log(e);
		}
		
		if(isReload){
			location.reload(); //
		}else{
			//
			translate.execute(); //

			//iframe LayuiAdmin  iframe iframe
			const iframes = document.querySelectorAll('iframe');
			for (let i = 0; i < iframes.length; i++) {
				const iframe = iframes[i];
				//  iframe  window 
				const iframeWindow = iframe.contentWindow; 
				try{       
					if(typeof(iframeWindow.translate) == 'object' && typeof(iframeWindow.translate.version) == 'string'){
						//iframe translate,iframe
						if(iframeWindow.translate.to != languageName){
							iframeWindow.translate.to = languageName;
							iframeWindow.translate.storage.set('to',languageName);	//
							iframeWindow.translate.execute();
						}
					}
				}catch(e){
					//try,
					console.log(e);
				}
			}
		}

		// translate.listener.start() listenerlistener.usetrue
		if(translate.listener.use == true && translate.listener.isStart == false){
			if(typeof(translate.listener.start) != 'undefined'){
				translate.listener.start();
			}
		}
	},
	
