	inProgressNodes:[], 
	//tagclass
	ignore:{
		tag:['style', 'script', 'link', 'pre', 'code'],
		class:['ignore','translateSelectLanguage'],
		id:[],
		/*
			 
			return truefalse
		*/
		isIgnore:function(ele){
			if(ele == null || typeof(ele) == 'undefined'){
				return false;
			}

			var parentNode = ele;
			var maxnumber = 100;	//
			while(maxnumber-- > 0){
				if(parentNode == null || typeof(parentNode) == 'undefined'){
					//
					return false;
				}

				//Tag
				//var tagName = parentNode.nodeName.toLowerCase(); //tag
				var nodename = translate.element.getNodeName(parentNode).toLowerCase(); //tag
				if(nodename.length > 0){
					//nodename
					if(nodename == 'body' || nodename == 'html' || nodename == '#document'){
						//
						return false;
					}
					if(translate.ignore.tag.indexOf(nodename) > -1){
						//ignore.tag  tag
						return true;
					}
				}
				

				//class name
				if(parentNode.className != null){
					var classNames = parentNode.className;
					if(classNames == null || typeof(classNames) != 'string'){
						continue;
					}
					//console.log('className:'+typeof(classNames));
					//console.log(classNames);
					classNames = classNames.trim().split(' ');
					for(var c_index = 0; c_index < classNames.length; c_index++){
						if(classNames[c_index] != null && classNames[c_index].trim().length > 0){
							//class name
							if(translate.ignore.class.indexOf(classNames[c_index]) > -1){
								//ignore.class  class
								return true;
							}
						}
					}					
				}

				//id
				if(parentNode.id != null && typeof(parentNode.id) != 'undefined'){
					//class name
					if(translate.ignore.id.indexOf(parentNode.id) > -1){
						//ignore.id  id
						return true;
					}
				}

				//
				parentNode = parentNode.parentNode;
			}

			return false;
		},

		/*
		 * 
		 *    
		 *  ['','']
		 */
		text:[],
		/*
			 textRegex  setTextRegexs text https://github.com/wangliangyu   translate.ignore.text 
		*/
		textRegex:[], 
		/*
			
			translate.ignore.setTextRegexs([//g, /[\u4a01-\u4a05]+/g]);
		*/
		setTextRegexs: function(arr) {
			if (!Array.isArray(arr)) throw new Error('');
			for (let i = 0; i < arr.length; i++) {
				if (!(arr[i] instanceof RegExp)) {
					throw new Error('' + i + 'RegExp');
				}
			}
			//this.textRegex = [...this.textRegex, ...arr];
			// es5 
			this.textRegex = this.textRegex.concat(arr); 
		},
	},
