		// start() 
		addListener:function(){
			if(translate.listener.isStart == true){
				console.log('translate.listener.start() ');
				return;
			}
			translate.listener.isStart = true; //
			
			// 
			translate.listener.config = { attributes: true, childList: true, subtree: true, characterData: true, attributeOldValue:true, characterDataOldValue:true };
			// 
			translate.listener.callback = function(mutationsList, observer) {
				var documents = []; //
				//console.log('--------- lisetner ');
			    // Use traditional 'for loops' for IE 11
			    for(let mutation of mutationsList) {
			    	let addNodes = [];
					if (mutation.type === 'childList') {
						if(mutation.addedNodes.length > 0){
							//
							addNodes = mutation.addedNodes;
							//documents.push.apply(documents, mutation.addedNodes);
						}else if(mutation.removedNodes.length > 0){
							//console.log('remove:');
							//console.log(mutation.removedNodes);
						}else{
							//console.log('not find:');
							//console.log(mutation);
						}
					}else if (mutation.type === 'attributes') {
						//console.log('The ' + mutation.attributeName + ' attribute was modified.');
					}else if(mutation.type === 'characterData'){
						//
						addNodes = [mutation.target];
						//documents.push.apply(documents, [mutation.target]);
					}

					// documents
					for(let item of addNodes){
						//console.log(item);

						//
						var isFind = false;
						for(var di = 0; di < documents.length; di++){
							if(documents[di].isSameNode(item)){
								isFind = true;
								break;
							}
						}
						if(isFind){
							break;
						}
						documents.push.apply(documents, [item]);
					}
	          	}
			    
				//console.log(documents.length);
				if(documents.length > 0){
					//10
					
					//node
					var translateNodes = [];
					//console.log(translate.inProgressNodes.length);
					for(let node of documents){
						//console.log('---type:'+node.nodeType);

						var find = false;
						for(var ini = 0; ini < translate.inProgressNodes.length; ini++){
							if(translate.inProgressNodes[ini].node.isSameNode(node)){
								//nodenode
								//console.log('');
								find = true;
								break;
							}
						}
						if(find){
							continue;
						}

						//console.log(node);
						let nodeid = nodeuuid.uuid(node);
						if(typeof(translate.listener.ignoreNode[nodeid]) != 'undefined'){
							if(translate.listener.ignoreNode[nodeid].addtime > Date.now() && typeof(node.nodeValue) == 'string' && node.nodeValue == translate.listener.ignoreNode[nodeid].text){
								//console.log('node listener'+nodeid);
								continue;
							}
						}

						// translateNodes
						translateNodes.push(node);
						//console.log('listener ++ '+node.nodeValue);
						//console.log(node);
					}
					if(translateNodes.length < 1){
						return;
					}
					//console.log('translateNodeslength: '+translateNodes.length);

					translate.execute(translateNodes);
					//setTimeout(function() {
					//	translate.execute(translateNodes); //,
					//}, 10); // task.execute()  settimeout  translate.inpr.....nodes  inpr...nodes listener inpr....nodes  inp.....nodes 
				}
			};
			// 
			translate.listener.observer = new MutationObserver(translate.listener.callback);
			// 
			var docs = translate.getDocuments();
			for(var docs_index = 0; docs_index < docs.length; docs_index++){
				var doc = docs[docs_index];
				if(doc != null){
					translate.listener.observer.observe(doc, translate.listener.config);
				}
			}
		},
