	/*

		 addNodeToQueue type translate.nodeQueue
		uuid, node, attribute  addNodeToQueue 
		
		word 
		lang  english
		beforeText  translate.nodeQueue 
		afterText  translate.nodeQueue 

	*/
	addNodeQueueItem:function(uuid, node, word, attribute, lang, beforeText, afterText){
		// key english
		if(translate.nodeQueue[uuid]['list'][lang] == null || typeof(translate.nodeQueue[uuid]['list'][lang]) == 'undefined'){
			translate.nodeQueue[uuid]['list'][lang] = new Array();
		}
		//console.log(word)
		//var word = text;	//
		var hash = translate.util.hash(word); 	//hash

		// key hash node
		if(translate.nodeQueue[uuid]['list'][lang][hash] == null || typeof(translate.nodeQueue[uuid]['list'][lang][hash]) == 'undefined'){
			translate.nodeQueue[uuid]['list'][lang][hash] = new Array();

			/*
			 * 
			 * key: nodes hashnodearray 
			 		nodes[index]['node'] node
			 		nodes[index]['attribute'] hashnodenodeValue node title
			 * key: original html
			 * beforeTextafterText: translate.nodeQueue 
			 */
			translate.nodeQueue[uuid]['list'][lang][hash]['nodes'] = new Array();
			translate.nodeQueue[uuid]['list'][lang][hash]['original'] = word;
			//
			//translate.nodeQueue[uuid]['list'][lang][hash]['translateText'] = translate.nomenclature.dispose(word); 
			translate.nodeQueue[uuid]['list'][lang][hash]['translateText'] = word;
			//console.log(word)



			//key nodes node
		}


		var isEquals = false; //queuenodehashnode
		if(typeof(node.isSameNode) != 'undefined'){	// isSameNode 
			for(var node_index = 0; node_index < translate.nodeQueue[uuid]['list'][lang][hash]['nodes'].length; node_index++){
				if(node.isSameNode(translate.nodeQueue[uuid]['list'][lang][hash]['nodes'][node_index]['node'])){
					//
					//console.log('')
					isEquals = true;
					//console.log(node)
					continue;
				}
			}
		}
		if(isEquals){
			//
			return;
		}

		//nodesnode
		var nodesIndex = translate.nodeQueue[uuid]['list'][lang][hash]['nodes'].length;
		translate.nodeQueue[uuid]['list'][lang][hash]['nodes'][nodesIndex] = new Array();
		translate.nodeQueue[uuid]['list'][lang][hash]['nodes'][nodesIndex]['node']=node; 
		translate.nodeQueue[uuid]['list'][lang][hash]['nodes'][nodesIndex]['attribute']=attribute;
		translate.nodeQueue[uuid]['list'][lang][hash]['nodes'][nodesIndex]['beforeText'] = beforeText;
		translate.nodeQueue[uuid]['list'][lang][hash]['nodes'][nodesIndex]['afterText'] = afterText;
		

	},

	//nodenode
	whole:{
		isEnableAll:false, //html false		

		enableAll:function(){
			translate.whole.isEnableAll = true;
		},

		/*
			  class tag id http://translate.zvo.cn/42563.html
		*/
		class:[],
		tag:[],
		id:[],

		// 
		// execute() 
		executeTip:function(){
			if(translate.whole.class.length == 0 && translate.whole.tag.length == 0 && translate.whole.id.length == 0){
				
			}else{
				console.log(' translate.whole  http://translate.zvo.cn/42557.html ');
			}

			if(translate.whole.tag.indexOf('html') > -1){
				console.log(' translate.whole.tag  html  body ');
			}
		},

		//
		/*
			 
			return truefalse
		*/
		isWhole:function(ele){

			if(translate.whole.isEnableAll){
				return true;
			}

			// class|tag|id  all=true 
			if((translate.whole.class.length == 0 && translate.whole.tag.length == 0 && translate.whole.id.length == 0) && translate.whole.isEnableAll == false){
				//false
				return false;
			}
			if(ele == null || typeof(ele) == 'undefined'){
				return false;
			}
			

			var parentNode = ele;
			var maxnumber = 100;	//
			while(maxnumber-- > 0){
				if(parentNode == null || typeof(parentNode) == 'undefined'){
					//
					return false;
				}

				//Tag
				//var tagName = parentNode.nodeName.toLowerCase(); //tag
				var nodename = translate.element.getNodeName(parentNode).toLowerCase(); //tag
				if(nodename.length > 0){
					//nodename
					if(nodename == 'html' || nodename == '#document'){
						//
						return false;
					}
					if(translate.whole.tag.indexOf(nodename) > -1){
						//ignore.tag  tag
						return true;
					}
				}
				

				//class name
				if(parentNode.className != null){
					var classNames = parentNode.className;
					if(classNames == null || typeof(classNames) != 'string'){
						continue;
					}
					//console.log('className:'+typeof(classNames));
					//console.log(classNames);
					classNames = classNames.trim().split(' ');
					for(var c_index = 0; c_index < classNames.length; c_index++){
						if(classNames[c_index] != null && classNames[c_index].trim().length > 0){
							//class name
							if(translate.whole.class.indexOf(classNames[c_index]) > -1){
								//ignore.class  class
								return true;
							}
						}
					}					
				}

				//id
				if(parentNode.id != null && typeof(parentNode.id) != 'undefined'){
					//class name
					if(translate.whole.id.indexOf(parentNode.id) > -1){
						//ignore.id  id
						return true;
					}
				}

				//
				parentNode = parentNode.parentElement;
			}

			return false;
		}
	},

