		/**
		 *  JSONArray 
		 *   array 6200 stringLength 2000 array 2000
		 * 		 maxSize = 5 5
		 * 		
		 * 		
		 *   array.toString()  [""] 
		 * @param array String ["",""]
		 * @param stringLength 
		 * @param maxSize  0
		 * @return 
		 * @author 
		 */
		 split:function(array, size, maxSize) {
		 	let orgsize = size;
		    let list = [];
		    // size
		    if(JSON.stringify(array).length <= size) {
		        list.push(array);
		    } else {
		        // String
		        let arrayStr = JSON.stringify(array).trim().substring(1, JSON.stringify(array).length - 1);

		        // size12
		        if (JSON.stringify(array).length - size <= 2) {
		            size = size - 4;
		            // 
		            let str1 = arrayStr.substring(0, arrayStr.lastIndexOf("\",\"")+1);
		            let str2 = arrayStr.substring(arrayStr.lastIndexOf("\",\"")+2);
		            list.push(JSON.parse("[" + str1 + "]"));
		            list.push(JSON.parse("[" + str2 + "]"));
		        } else {
		            size = size - 2;
		            // 
		            let index = 0;
		            while (index - arrayStr.length < 0) {
		                // 
		                let s = "";
		                if ((index+size) - arrayStr.length >= 0) {
		                    s = arrayStr.substring(index);
		                } else {
		                    s = arrayStr.substring(index, (index+size));
		                }
		                // 
		                let endIndex = s.length;
		                // +1
		                let startNeedAdd = 1;
		                // 
		                if (s.endsWith("\"")) {
		                    // 
		                    if (s.endsWith("\",\"")) {
		                        // 
		                        endIndex-=2;
		                    } else if (!s.startsWith("\"")) {
		                        // 
		                        // 
		                        let la = s.lastIndexOf("\",\"");
		                        endIndex = la + 1;
		                    }
		                } else if (s.endsWith("\",")) {
		                    // 
		                    endIndex-=1;
		                } else {
		                    // 
		                    // 
		                    let la = s.lastIndexOf("\",\"");
		                    endIndex = la + 1;
		                    // endIndex0
		                    if (endIndex <= 0) {
		                        // 
		                        if (s.startsWith("\"")) {
		                            // -1
		                            endIndex = s.length - 1;
		                        } else {
		                            // -2
		                            endIndex = s.length - 2;
		                        }
		                        if (!s.endsWith("\"")) {
		                            // -1
		                            startNeedAdd = 0;
		                        }
		                    }
		                }
		                // 
		                let s2 = "";
		                if (endIndex - s.length > 0 || endIndex - 0 == 0) {
		                    s2 = s;
		                    endIndex = endIndex + s2.length;
		                } else {
		                    s2 = s.substring(0, endIndex);
		                }
		                if (!s2.startsWith("\"") && !s2.startsWith(",\"")) {
		                    // 
		                    s2 = "\"" + s2;
		                }
		                if (!s2.endsWith("\"")) {
		                    // 
		                    s2 = s2 +"\"";
		                }
		                // 
		                index += (endIndex + startNeedAdd);
		                // list
		                s2 = "[" + s2 + "]";
		                try {
		                    list.push(JSON.parse(s2));
		                } catch (e) {
		                    // 
		                    index = index - (endIndex + startNeedAdd) + 1;
		                }
		            }
		        }
		    }
			// maxSize
			if (maxSize && maxSize > 0) {
				list = translate.util._splitMaxSize(list, orgsize, maxSize);
			}
		    return list;
		},
		/**
		 * splitmaxSize
		 * 	private
		 * @param array 
		 * @param size 
		 * @param maxSize 
		 * @author 
		 */
		_splitMaxSize:function(array, size, maxSize) {
			// console.log("------ splitMaxSize run ------")

			// 
			let list = [];
			// 
			let tmp = [];

		 	// 
			array.forEach(function(arr, index) {
				// 
				arr = tmp.concat(arr);
				// 
				let length = arr.length;
				// maxSize
				if (length > maxSize) {
					// N
					let firstArray = arr.slice(0, maxSize);
					// 
					let secondArray = arr.slice(maxSize);

					// 
					let len = 1;
					while (JSON.stringify(firstArray).length > size) {
						// 
						firstArray = arr.slice(0, maxSize - len);
						secondArray = arr.slice(maxSize - len);
						len++;
						if (len >= arr.length+1) {
							break;
						}
					}

					// 
					list.push(firstArray);
					// 
					tmp.length = 0;
					tmp = secondArray;
				} else {
					// 
					// 
					let firstArray = arr;
					let secondArray = [];
					let len = 1;
					while (JSON.stringify(firstArray).length > size) {
						// 
						firstArray = arr.slice(0, maxSize - len);
						secondArray = arr.slice(maxSize - len);
						len++;
						if (len >= arr.length+1) {
							break;
						}
					}

					// 
					list.push(firstArray);
					// 
					tmp.length = 0;
					tmp = secondArray;
				}

			});

			// 
			if (tmp.length > 0) {
				let tmpl = [];
				tmpl.push(tmp);
				// 
				let l = translate.util._splitMaxSize(tmpl, size, maxSize);
				list = list.concat(l);
			}

			return list;
		},
