import request from './request'
// 
export const languageApi = {
    // 
    getUserSetting: async () => {
        return await request({
            url: '/user_init/get_settings',
            method: 'get',
        })
    },

    // 
    setUserLanguage: async (data) => {
        return await request({
            url: '/user_init/set_user_lang',
            method: 'post',
            data: {user_lang: data}
        })
    },

    // 
    startPanel: async () => {
        return await request({
            url: '/panel/start',
            method: 'post'
        })
    }
}